.loginContainer {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.loginTitle {
    font-weight: 800;
    font-size: 100px;
    color: #516EFF;
}

.loginInputContainer {
    width: 700px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.loginInputContainerName {
    margin-top: 60px;
    font-weight: 800;
    font-size: 32px;
    color: #4B4B4B;
}

.loginInputContainer input {
    margin-top: 10px;
    width: 100%;
    height: 50px;
    border: none;
    border-bottom: 1px solid #000;
    font-size: 24px;
}
.loginInputContainer input:focus {
    outline: none;
    border: none;
    border-bottom: 2px solid #516EFF;
}
.loginInputContainer input::-webkit-input-placeholder {
    text-align: center;
}
.loginInputContainer input::placeholder {
    text-align: center;
}

.loginButtonContainer {
    margin-top: 100px;
    position: relative;
    width: 700px;
    height: 50px;
    display: flex;
}

.loginButton {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 300px;
    height: 50px;
    border-radius: 30px;
    background: #516EFF;
    color: #fff;
    font-weight: 800;
    font-size: 17px;
}

.loginJoinText {
    position: absolute;
    top: 16px;
    right: 60px;
    font-weight: 500;
    font-size: 17px;
    cursor: pointer;
    border-bottom: 1px solid #000;
}

@media (max-width: 768px) {
    .loginTitle {
        font-size: 32px;
    }

    .loginInputContainer {
        width: 340px;
    }

    .loginInputContainerName {
        font-size: 16px;
    }
    
    .loginInputContainer input {
        font-size: 14px;
    }

    .loginButtonContainer {
        margin-top: 60px;
        width: 340px;
    }

    .loginButton {
        width: 170px;
        height: 40px;
        font-size: 12px;
    }
    
    .loginJoinText {
        right: 20px;
        font-size: 12px;
    }
}
