.homeContainer {
    width: 100vw;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 100px 100px;
    grid-column-gap: 100px;
    -webkit-column-gap: 100px;
            column-gap: 100px;
    grid-row-gap: 100px;
    row-gap: 100px;
    text-align: center;
    flex-wrap: wrap;
}

.homeMenuButton {
    width: 340px;
    height: 450px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 60px;
    gap: 60px;
    text-align: center;
    background: #FAF8F8;
    border-radius: 10px;
    cursor: pointer;
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.homeMenuButton:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

